<script>
import Vue from 'vue'
import NavHead from '@/components/head.vue'
import { feedback } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
import { Icon, Switch, Toast, DropdownMenu, DropdownItem } from 'vant'

Vue.use(Icon).use(Switch).use(Toast).use(DropdownMenu).use(DropdownItem)
export default {
  components: { NavHead },
  data () {
    return {
      languageStatus: '',
      checked: true,
      curHeight: '',
      title: '设置',
      isShow: false,
      leveIndex: 1,
      witch_language: 0,
      value2: 'a',
      option1: [
        { text: 'Chinese', value: 0 },
        { text: 'Engish', value: 1 },
        { text: 'German', value: 2 },
        { text: 'French', value: 3 },
        { text: 'Japanese', value: 4 },
        { text: 'Korean', value: 4 }
      ],
      feedbackData: {
        u_id: '', // int用户ID
        leve: '', // int星星数量
        content: '' // 内容
      }
    }
  },
  created () {
    this.beforeMount()
    this.feedbackData.u_id = sessionStorage.getItem('u_id')
    this.getLanguageStatus()
  },
  methods: {
    // getLanguage (value) {
    //   console.log('llllll')
    //   console.log(value)
    //   if (value === 0) {
    //     this.changeLan()
    //     // window.localStorage.setItem('language', 'zh')
    //   } else {
    //     this.changeLan()
    //   }
    // },
    getLanguageStatus () {
      console.log(this.$store.state.language)
      this.languageStatus = this.$store.state.language
    },
    changeLan () {
      this.$changeLan()
      console.log(localStorage.getItem('language'))
      console.log(this.$store.state)
      this.getLanguageStatus()
    },
    imgclick (index) {
      this.leveIndex = index + 1
    },
    userBut () {
      var that = this
      if (that.feedbackData.content === '') {
        that.$toast({ message: '内容不能为空', duration: 1500 })
        return
      }
      that.feedbackFun()
      //
    },
    userFeedback () {
      this.isShow = true
    },
    showBox () {
      this.isShow = false
    },
    beforeMount (height) {
      var h = document.documentElement.clientHeight || document.body.clientHeight
      this.curHeight = h
    },
    feedbackFun () {
      var that = this
      that.feedbackData.leve = that.leveIndex
      var data = objKeySort(that.feedbackData)
      var token = sgin(data)
      this.feedbackData.token = token
      feedback(that.feedbackData).then(res => {
        if (res.code === 0) {
          that.$toast({ message: '反馈成功', duration: 1500 })
          that.feedbackData.content = ''
          that.isShow = false
          delete that.feedbackData.token
        }
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        that.feedbackData.content = ''
        delete that.feedbackData.token
      })
    },
    toSignOut () {
      sessionStorage.setItem('u_id', '')
      this.$router.push({
        path: '/login',
        query: {
        }
      })
    }
  }
}
</script>
